@import url(https://fonts.googleapis.com/css?family=Roboto);

body{
  background-color: #000;
  color: #fff;
}

.hero{
  height: 40vh;
  background-size: cover;
  background-position: center;

}
.txtShadow{
  text-shadow: 5px 5px 6px rgba(150, 150, 150, 1);
}
aside{
  display: none;
}

main{
  position: absolute;
padding: 20px;
/* 60px the heigh of topNav + 20px match main padding = 80 */
padding-top: 80px;
}

.song{
  cursor: pointer;
}

@media screen and (min-width: 992px){
  main{
    position: absolute;
    width: 80vw;
    left: 20vw;
  padding: 20px;
  /* 60px the heigh of topNav + 20px match main padding = 80 */
  padding-top: 80px;
  }

  aside{
    display: block;
    /* background-color: rgba(255, 0, 0, 0.37); */
    width: 20vw;
    height: 100vh;
    position: absolute;
    position: fixed;
    top: 60px;
  }
}
header .burger {
  display: none;
}
.nav_top{
background-color: rgb(18, 17, 17);
height: 60px;
position: fixed;
/* top: 0; */
z-index: 10;
}

header a {
  color:rgb(252, 252, 252);
  padding: 0 12px;
  text-decoration: none;
}


@media (max-width:991px){
  header .burger {
    display: block;
    cursor: pointer;
  }

  header nav { 
    position: absolute;
    right:0;
    top:60px;
    width:70% !important;
    background: rgb(18, 17, 17);
    padding:0 !important;
    display: none;
    
  }

  header nav a{
    display: block;
    padding:8px;
    text-align: center;
    border-bottom: 2px solid #333; 
  }
  header nav a:hover{
    background: #333;
    color:white;
  }
}
.sideNav{
  width: 20vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.133);
  position: fixed;
}
.navLinks{
  list-style-type: none;
  padding: 0;
}
.navLinks li{
  border-bottom: 1px solid #fff;
  padding: 12px;
  text-align: center;
}
.navLinks li:hover{
background-color: rgba(255, 247, 0, 0.284);
}
.controls{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  /* background-color: rgb(81, 196, 23); */
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.player{
    height: 70vh;
display: grid;
}
.hoverable:hover{
  color: #EB5E27;
-webkit-transform: scale(1.15);
        transform: scale(1.15);
transition: all .3s ease-in;
cursor: pointer;
}
.hoverable{
  transition: all .25s ease-out;
}


.songCard h3{
	text-align: center;
	color: #FFF;
	text-transform: uppercase;
  
}
*{
	transition-duration: 0.6s;
}
.imgClass{
  width: 100%;
}
.songCard h3, i{
	color: #FFF;
	opacity: 0;
}

.card:hover h3, .card:hover i{
	opacity:1;
	-webkit-transform:translateY(10px);
	        transform:translateY(10px);

}
.center{
width: 300px;
height: 300px;
display: -webkit-flex;
display: flex;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: center;
        align-items: center;
}
.card{
	overflow: hidden;
	background-color: #000;
}
.songCard i{
	margin-top: 25px;
  cursor: pointer;
}
.card:hover i{
	-webkit-transform:rotate(360deg);
	        transform:rotate(360deg);
  color: orange;
}
.card:hover img{
	-webkit-transform:scale(1.5) rotate(10deg);
	        transform:scale(1.5) rotate(10deg)
}
.moare{
	color:#FFF;
	text-align: center;
  position: absolute;
  padding: 20px;
}
.actions{
	text-align: center;
}
.card:hover img{
	opacity:0.8
}
.card:after{
	  position: absolute;
    top: 5%;
    left: 5%;
    right: 5%;
    bottom: 5%;
    content: '';
    opacity: 1;
    display: block;
    transition: all .35s ease-out;
	border-top: 3px solid #fff;
    border-bottom: 3px solid #fff;

	-webkit-transform: scaleX(0)!important;
    transform: scaleX(0)!important;
}

.card:hover:after{
		/* z-index: 1000; */
	-webkit-transform: scaleX(1)!important;
    transform: scaleX(1)!important;
}
.card:before{
	  position: absolute;
    top: 5%;
    left: 5%;
    right: 5%;
    bottom: 5%;
    content: '';
    opacity: 1;
    display: block;
    transition: all .35s ease-out;
	border-right: 3px solid #fff;
    border-left: 3px solid #fff;
    -webkit-transform-origin: 100% 0;
    transform-origin: 100% 0;
	-webkit-transform: scaleY(0)!important;
    transform: scaleY(0)!important;
}

.card:hover:before{
		z-index: 1;
	-webkit-transform: scaleY(1)!important;
    transform: scaleY(1)!important;
}
.outterPopUp{
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  z-index: 1000;
  }
.content{
  color: rgb(255, 255, 255);
  padding: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: start;
          justify-content: start;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  grid-gap: 10px;
  gap: 10px;
  }
  .img{
    width: 160px;
    height: 160px;
    background-size: cover;
    border-radius: 50%;
  }

  .innerPopUp{
    padding-top: 25px;
    width: 35vw;
    height: 40vh;
    background-color: rgb(89, 86, 86);
    border-radius: 25px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    
    }

@media screen and (max-width: 992px) {

    .innerPopUp{
    width: 85vw;
    height: 40vh;
    }
   
}

